import { createReactEditorJS } from 'react-editor-js'

const Editor = () => {
  const ReactEditorJS = createReactEditorJS();

  return (
    <ReactEditorJS
      defaultValue={{
        blocks: [
          {
            id: "1",
            type: "paragraph",
            data: {
              text: "   ..."
            }
          },
          {
            id: "2",
            type: "paragraph",
            data: {
              text: " : ..."
            }
          },
          {
            id: "2",
            type: "paragraph",
            data: {
              text: "<b> </b>"
            }
          },
          {
            id: "3",
            type: "paragraph",
            data: {
              text: "    ,   "
            }
          },
        ]
        }}
    />
  );
}

export default Editor;